.cont {
  background-color: black;
}

@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-250px * 7));
  }
}

.slider {
  height: 100px;
  margin: auto;
  overflow: hidden;
  position: relative;
  width: auto;
}

.slider .slider-track {
  display: flex;
  animation: scroll 40s linear infinite;
  width: calc(250px * 14);
}

.slider .slide {
  height: 100px;
  width: 250px;
}
